import React from 'react'
import styled from 'styled-components';
import MeuFundo from './../Assets/Background.png'
import mypic from './../Assets/my-pic.jpg'
import {Link} from 'react-router-dom'

let StyledDiv = styled.div`
    position: relative;
    overflow-y: auto;  
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: url(${MeuFundo});
    background-size: cover;
    background-position: center;
    .my-nav{
        display: flex;
        /* background-color: darkgrey; */
        align-items: center;
        width: 100%;
        height: 20%;
        .my-link{
            color: white !important;
        }
        .my-link:hover{
            text-decoration-line: line-through;
            color: rgb(255, 8, 243) !important;
            transition: color 1s;
        } 
    }
    .my-body{
        display: flex;
        padding: 1%;
        flex-direction: column;
        /* background-color: darkmagenta; */
        width: 100%;
        height: 100%;
        .my-title{
            color: white;
            padding: 0.7%;
            background-color: darkmagenta;
            border-radius : 45px;
        }
        .my-card{
            position: relative;
            padding: 1.2%;
            width: 35%;
            @media(max-width: 768px){
                width: 85%;
            }
            background-color: darkmagenta;
            color: white;
            transition: all 0.8s;
            :hover{
                /* transform: scale(1.05); */
                /* background-color: #cecece; */
                /* color: darkmagenta; */
            }
            
        }
        .description{
            overflow-y: auto;
            p{font-size: 14px;}

            @media(max-width: 768px){
                p{font-size: calc(1.2vw + 1.2vh);}
            }
        }
    }
    .my-footer{
        display: flex;
        justify-content: center;
        align-items: center;
        /* background-color: darkgreen; */
        width: 100%;
        height: 12%;
    }
`
export default class Home extends React.Component{
    render(){
        return (
            <StyledDiv>
                <div className="my-nav">
                    <div className="w-100 d-flex justify-content-center">
                        <ul class="nav justify-content-end">
                            <li class="nav-item">
                                <Link to="/" className="nav-link active d-flex align-items-center">
                                    <h5 className="my-link">Home</h5>
                                </Link>
                            </li>
                            <li class="nav-item">
                                <Link to="/P2" className="nav-link active">
                                    <h5 className="my-link">Projetos</h5>
                                </Link>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="my-body">
                    <div className="my-card mx-auto d-flex flex-column rounded">
                        <div className="w-50 mx-auto d-flex justify-content-center">
                            <img alt="" src={mypic} className="w-75 rounded-circle"></img>
                        </div>
                        <div className="w-75 mx-auto d-flex justify-content-center mt-3">
                            <h4>Lukas Dias</h4>
                        </div>
                        <div className="w-100 d-flex justify-content-center">
                            Front-End e Desenvolvimento de jogos
                        </div>
                    </div>
                   
                    <div className="w-100 h-75 d-flex justify-content-center align-items-baseline mt-3">
                        <div className="w-75 description d-flex flex-column justify-content-center text-justify bg-dark rounded p-2">
                            <h1 className="text-white">Quem sou?</h1>
                            <p className="text-white">Me chamo Lukas Henrique Dias trabalho com front-end e
                             sou aspirante a game-dev, estou atualmente no quarto período do curso de Ciência da Computação na Universidade Federal do Maranhão, possuo experiência nas linguagens: C, Lua, Javascript, Java, Python.</p>
                            {/* <p className="text-white">Fiz um jogo mini metroidvania no meu primeiro período de curso (para acessar basta clicar no canto superior direito da tela).</p> */}
                            <p className="text-white">Sou membro do laboratório Telemídia-MA, tendo participado do projeto chamado Cosmo que foi implementado para teste na turma de 2019.1 do curso de Ciência da Computação</p>
                        </div>
                    </div>
                </div>
                <div className="my-footer"><h1 className="text-white"></h1></div>
            </StyledDiv>
        )
    }
}