import React from 'react'
import styled from 'styled-components';
import MeuFundo from './../Assets/Background.png'
import Projeto1 from './../Assets/Projeto-1.png'
import {Link} from 'react-router-dom'

let StyledDiv = styled.div`
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    overflow-y: auto;
    flex-direction: column;
    background: url(${MeuFundo});
    background-size: cover;
    background-position: center;
    .my-nav{
        display: flex;
        /* background-color: darkgrey; */
        align-items: center;
        width: 100%;
        height: 20%;
        .my-link{
            color: white !important;
        }
        .my-link:hover{
            text-decoration-line: line-through;
            color: rgb(255, 8, 243) !important;
            transition: color 1s;
        } 
    }
    .my-body{
        display: flex;
        flex-direction: column;
        /* background-color: darkmagenta; */
        width: 100%;
        height: 100%;
        .my-title{
            color: white;
            padding: 0.7%;
            background-color: darkmagenta;
            border-radius : 45px;
        }
    }
    .my-footer{
        display: flex;
        justify-content: center;
        align-items: center;
        /* background-color: darkgreen; */
        width: 100%;
        height: 12%;
    }
`
export default class Home extends React.Component{
    render(){
        return (
            <StyledDiv>
                <div className="my-nav">
                    <div className="w-100 d-flex justify-content-center">
                        <ul class="nav justify-content-end">
                            <li class="nav-item">
                                <Link to="/" className="nav-link active d-flex align-items-center">
                                    <h5 className="my-link">Home</h5>
                                </Link>
                            </li>
                            <li class="nav-item">
                                <Link to="/P2" className="nav-link active">
                                    <h5 className="my-link">Projetos</h5>
                                </Link>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="my-body">
                    <div className="w-100 d-flex justify-content-center">
                        <h1 className="text-bold text-white">Meus Projetos: </h1>
                    </div>
                    <div className="w-100 h-100 d-flex flex-column align-items-center">
                        <div className="w-100 d-flex justify-content-center flex-column">
                            <div className="w-100 d-flex justify-content-center">
                                <h4 className="mb-2 my-title">Lament of Laurence</h4>
                            </div>
                            <div className="w-100 d-flex justify-content-center">
                                <img alt="" src={Projeto1} className="w-25 rounded"></img>
                            </div>
                        </div>
                        <div className="w-100 d-flex justify-content-center align-self-center mt-2 text-white">
                            <div className="w-75 d-flex justify-content-center">
                                <h6>*Print do mapa tiled utilizado no jogo acima*</h6>
                            </div>
                        </div>
                        <div className="w-75 d-flex justify-content-center">
                            <h6 className="text-white">Jogo feito para a terceira nota da matéria Algoritmos 1, usando a linguagem Lua e o framework LOVE2D</h6>
                        </div>
                    </div>
                </div>
                <div className="my-footer"><h1 className="text-white"></h1></div>
            </StyledDiv>
        )
    }
}