import React from 'react';
import {HashRouter, Switch, Route} from 'react-router-dom';

import P1 from './Pages/1'
import P2 from './Pages/2'

export default () => (
    <HashRouter>
        <Switch>
            <Route exact path="/" component={P1}></Route>
            <Route exact path="/P2" component={P2}></Route>
        </Switch>
    </HashRouter>
);